<template>
    <div class="myview">
        <div class="mytop">
          <div class="img">
            <img src="https://cs.xinpianchang.com/passport/default.jpg" alt="">
          </div>
          <p v-if="nickname">{{nickname}}</p>
          <router-link tag="p" to="/loginview" v-else>点击登录</router-link>
        </div>
        <div class="follow">
          <div class="followee">
            <p class="count">0</p>
            <p class="text">关注</p>
          </div>
          <div class="follower">
            <p class="count">0</p>
            <p class="text">粉丝</p>
          </div>
        </div>
        <div class="content">
          <div class="collection" @click="goMycollect">
            <h3>我的收藏</h3>
            <span class="iconfont icon-jinrujiantou"></span>
          </div>
          <div class="like" @click="goMylike">
            <h3>我的喜欢</h3>
            <span class="iconfont icon-jinrujiantou"></span>
          </div>
          <div class="history">
            <h3>浏览记录</h3>
            <span class="iconfont icon-jinrujiantou"></span>
          </div>
          <div class="about">
            <h3>关于我们</h3>
            <span class="iconfont icon-jinrujiantou"></span>
          </div>
        </div>
        <div class="quit" v-if="nickname != ''" @click="signOut">退出登录</div>
        <tab-bar :tab-index="tabIndex"></tab-bar>
    </div>
</template>

<script>
import TabBar from "../components/public/TabBar.vue";
export default {
  data() {
    return {
      tabIndex: 3,
      nickname:'',
      avatar: '',
    };
  },
  methods:{
    getMyInfo(){
      let userMsg = localStorage.getItem("userlist");
      let userlist = [];
      if(!userMsg){
        return;
      }
      userlist = JSON.parse(userMsg);
      let index = userlist.findIndex((item) => item.type == 'login')
      if(index == -1){return;}
      this.nickname = userlist[index].nickname;
      this.avatar = userlist[index].avatar;
    },
    signOut(){
      let userMsg = localStorage.getItem("userlist");
      let userlist = [];
      if(!userMsg){
        return;
      }
      userlist = JSON.parse(userMsg);
      let index = userlist.findIndex((item) => item.type == 'login');
      delete userlist[index].type;
      userlist = JSON.stringify(userlist)
      localStorage.setItem("userlist", userlist);
      this.$router.push("/loginview")
    },
    goMylike(){
      if(this.nickname == ''){
        this.$toast("请先登录!")
        return;
      }
      this.$router.push("/mylikeview")
    },
    goMycollect(){
      if(this.nickname == ''){
        this.$toast("请先登录!")
        return;
      }
      this.$router.push("/mycollectview")
    }
  },
  created(){
    this.getMyInfo();
  },
  components: {
    TabBar,
  },
};
</script>

<style lang="less">
.myview {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#f1e0de 15%, #f3f4f5 30%);
  .mytop {
    padding-top: 34px;
    height: 55px;
    font-size: 16px;
    line-height: 55px;
    display: flex;
    padding-left: 20px;
    .img{
      width: 55px;
      height: 55px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 55px;
        height: 55px;
      }
    }
    p {
      margin-left: 15px;
    }
  }
  .follow{
    margin-top: 26px;
    display: flex;
    .followee,.follower {
      flex: 1;
      text-align: center;
      font-size: 12px;
      line-height: 20px;
      .text {
        // font-size: 14px;
        color: #a6aab4;
      }
    }
    .followee{
      position: relative;
    }
    .followee::after{
      display: block;
      content: "";
      width: 1px;
      height: 20px;
      background-color: #a6aab4;
      position: absolute;
      right: 0;
      top: 10px;
    }
  }
  .content {
    width: 94.6%;
    margin: 0 auto;
    margin-top: 40px;
    background-color: #fefefe;
    border-radius: 6px;
    .collection,.like,.history,.about {
      // width: 100%;
      height: 50px;
      line-height: 50px;
      margin:0 15px;
      border-bottom: 1px solid #f3f4f5;
      display: flex;
      justify-content: space-between;
      h3 {
        font-size: 14px;
      }
      .iconfont {
        color: #a6aab4;
      }
      
    }
  }
  .quit {
    width: 80%;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    margin-top: 40px;
    background-color: #e45e51;
    text-align: center;
    color: #fff;
    font-size: 16px;
    border-radius: 10px;
  }
}
</style>